import { useState } from "react";
import { useHistory } from "react-router-dom";

export default function Card(props) {
    const { data, isFavourite } = props;

    const [checked, setChecked] = useState(false);

    const handleChange = (eve) => {
        const value = eve.target.checked;
        setChecked(value)
        if (value) {
            const getLocal = JSON.parse(localStorage.getItem("favorites"));
            if (getLocal !== null) {
                localStorage.setItem("favorites", JSON.stringify({ ...getLocal, [data.Title]: data }));
            } else {
                localStorage.setItem("favorites", JSON.stringify({ [data.Title]: data }));
            }
        }
        else {
            const getLocal = JSON.parse(localStorage.getItem("favorites"));
            getLocal && delete getLocal[data.Title];

        }

    }
    let history = useHistory();

    const handleImageClick = () => {
        history.push(`/${data.imdbID}`);
    }

    return (
        <div className="movie-card">
            {isFavourite ? (
                <div className="favorites">
                    <label htmlFor="fav">Favourites</label>
                    <input id="fav" type="checkbox" checked={checked} onChange={handleChange} />
                </div>
            ) : null}
            {isFavourite ? (
                <img onClick={handleImageClick} src={data.Poster} alt={data.Title} />
            ) :
                <img src={data.Poster} alt={data.Title} />
            }
            <div>Movie title: {data.Title}</div>
            <div>Release Date: {data.Year}</div>
            <div>Type: {data.Type}</div>
        </div>
    )
}