import { useState } from 'react';
import { Route, Switch, useHistory, useParams } from 'react-router-dom';
import Card from './Card';
import { getUrl, MovieService } from './movie.service';

export default function Entertainment(props) {
    let history = useHistory();

    const [list, setList] = useState([]);
    const [searchValue, setSearchValue] = useState("");
    const [selectValue, setSelectValue] = useState("all");
    const [error, setError] = useState("");
    let { imdbId } = useParams();

    const handleClick = () => {
        const url = getUrl({
            s: searchValue,
            ...(selectValue !== "all") ? { type: selectValue } : {}
        });
        MovieService(url).then(json => {
            if (json.Response === "False") {
                throw new Error(json.Error);
            } else {
                setList(json.Search);
                history.push(`/list`);
            }

        }
        ).catch(err => {
            setError(err['message'])
        });
    }

    const handleSearch = (eve) => {
        const value = eve.target.value;
        setSearchValue(value)
    }

    const handleSelect = (eve) => {
        const value = eve.target.value;
        setSelectValue(value)
    }

    const handleImgClick = (val) => {
        props.handleImgClick(val)
    }
    return (
        <>
            <div className="search">
                <input type="search" onChange={handleSearch} placeholder="Search here" value={searchValue} />
                <select value={selectValue} onChange={handleSelect}>
                    <option value="all">all</option>
                    <option value="movie">movies</option>
                    <option value="series">series</option>
                    <option value="episode">episode</option>
                </select>
                <button onClick={handleClick}>Search</button>
            </div>
            <Switch>
                <Route exact path="/list">
                    <section className="listing">
                        {(list.length) ?
                            (
                                list.map((t, i) => (
                                    <Card data={t} key={i} isFavourite handleImgClick={handleImgClick} />
                                )
                                )
                            ) :
                            error ? error : "No results found"
                        }
                    </section>
                </Route>
                <Route path="/:imdbId" children={() => {
                    const data = list.find(t => t.imdbId === imdbId)
                    return (
                        <div>
                            <Card data={data} />
                        </div>
                    )

                }}
                >
                </Route>
                <Route path="/" children={() => (
                    <div>
                        <h3>
                            No results found
                        </h3>
                    </div>
                )}
                >
                </Route>
            </Switch>
        </>
    )
}