export default function Favorites(props) {
    const getLocal = JSON.parse(localStorage.getItem("favorites"));

    return (
        <section className="favorite-list">
            {
                (getLocal !== null) ?
                    Object.values(getLocal).map((t, i) => (
                        <div className="movie-card" key={i}>
                            <img src={t.Poster} alt={t.Title} />
                            <div>Movie title: {t.Title}</div>
                            <div>Release Date: {t.Year}</div>
                            <div>Type: {t.Type}</div>
                        </div>
                    )) : "No Favorites"

            }
        </section>
    )
}